$bgcolor-content-standard: lightgrey;

// Key color 1
$key-color-main: #003571;
$key-bgcolor-main: #003571;
$key-bgcolor-main-color: white;

$key-color-content-main: #60810e;

// +++++++++++++++++++++++++++++++++++++++++++++++++++

.theme-key-color-content {
    color: $key-color-content-main;
}

.theme-key-bgcolor-1 {
    background-color: #60810e;
}

.th-bgcolor-content-standard {
    background-color: $bgcolor-content-standard;
}

.th-key-color-main {
    color: $key-color-main;
}

.th-key-bgcolor-main {
    background-color: $key-bgcolor-main;
}

// +++++++++++++++++++++++++++++++++++++++++++++++++++

.c-content-container {
    p > a {
        color: $key-color-content-main;
    }
}

.c-special-container.th-key-bgcolor-main {
    h3,
    h4,
    p,
    li,
    p > a {
        color: $key-bgcolor-main-color;
    }
}

// special content elements
.c-quote__image {
    svg {
        fill: $key-color-content-main;
    }
}
